.barraSuperior {
   display: flex;
   justify-content: space-between;
   align-items: center;
   height: 80px;
   position: fixed;
   width: 100%;
   background-color: #06f8d836; 
   border-bottom: 2px solid rgb(250, 248, 248);
   margin-top: -1px;
   top: 0;
   left: 0;
   z-index: 9999;
}

.contenedorNav {
   width: 120px;
   height: 60px;
   margin-top: 8.4px;
}

.logo {
   display: flex;
   width: 100px;
   height: auto;
   margin-top: 10px;
   margin-left: 40%; /* Centrar horizontalmente el logo */
   margin-right: 530px;
   /* justify-content: center; */
}

.logo:hover {
   transition: transform 0.2s;
   transform: scale(1.5);
   box-shadow: 0px 0px 100px rgba(10, 222, 250, 0.76);
   background-color: rgba(10, 222, 250, 0.247); 
   border-radius: 50px;
   /* margin-top: 80px; */
   
}


.infoBoton {   
   justify-content: auto;
   margin-right: 0px;
   margin-left: 20px;
   border-radius: 5px;
   height: 40px;
   width: 100px;
   background-color: rgba(96, 241, 241, 0.473);
   color: bisque;
   padding: 5px;
   transition: background-color 0.5s; 
   font-size: 18px;
   border: 0;
   margin-top: 9px;
   /* box-shadow: 1px 1px rgb(220, 221, 223);  */
   font-size: 12px;
   border: 1px solid rgb(10, 250, 2);
}

.infoBoton:hover {
   background-color: rgba(135, 245, 245, 0);
   transition: background-color 0.5s; 
   /* box-shadow: 2px 2px rgb(220, 221, 223);  */
   box-shadow: 0px 0px 100px rgba(248, 248, 248, 0.76);
   border: 1px solid rgb(182, 255, 13);
   border-radius: 5px;
}
